@import '../../variables';

#aligent-checkitout {
  #payment-div{

    .payment-info{
      margin-top: 20px;
    }

    .payment-separator{
      margin-top: 25px;
      margin-bottom: 25px;
      border-bottom: 1px solid $colour-border;
    }

    .afterpay-link{
      color: black;
      text-decoration: underline;
    }


    .promo {
      display: flex;
      .input-promo{
        flex: 4;
        margin-right:10px;
      }
      .submit-promo{
        flex: 1;
        justify-content: flex-end;
      }
    }

    .gift {
      display: flex;
      .input-gift{
        flex: 4;
        margin-right:10px;
      }
      .submit-gift{
        flex: 1;
        justify-content: flex-end;
      }
    }

    .expiry-cvc {
      display: flex;
      .expiry {
        display: block;
        padding-right:5px;

      }
      .cvc {
        display: block;
        padding-left:5px;
      }
    }

    .payment-options{
      display: flex;
      margin-top: 25px;

      .card {
        display: flex;
        flex: 1;
        justify-content: flex-end;
        cursor: pointer;
      }

      .paypal {
        display: flex;
        flex: 1;
        justify-content: center;
        cursor: pointer;
      }

      .afterpay {
        display: flex;
        flex: 1;
        justify-content: flex-start;
        cursor: pointer;
      }
    }

  }

