@import './variables';

#aligent-checkitout {

  .email-form {
    margin-top: 25px;
  }

  .btn-place-order {
    margin-top: 25px;
  }

  .form-label {

  }

  #message{
    margin-top: 50px;
  }

  input[type="text"] {
    background: $colour-white;
    padding-left: 10px;
    height:50px;
    font-size:20px;
    line-height: normal;
    border-radius: 3px;
  }

  input[type="password"] {
    border-radius: 3px;
    background: $colour-white;
    padding-left: 10px;
    height:50px;
    font-size:20px;
    line-height: normal;
  }

  input[type="submit"], input[type="button"]{
    border-radius: 3px;
    border: 1px solid $colour-border;
    background: $colour-button;
    border-radius: 3px;
    width: 100%;
    padding: 15px;
    color: $colour-white;
    font-weight: bold;
  }

  input[type="button"]:hover, input[type="submit"]:hover {
    background: $colour-button-over;
  }

  .danger {
    color: $colour-danger;
    font-weight:bold;
  }
  .warning {
    color: $colour-warning;
    font-weight:bold;
  }
  .warning-border {
    border: 1px solid $colour-pink;
  }
  .info {
    color: $colour-info;
    font-weight:bold;
  }
  .success {
    color: $colour-success;
    font-weight:bold;
  }

}
